/* General App Settings */
.App {
  background-color: #282c34;
}

.App-body {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  font-size: 100px;
}

.App-header {
  display: flex;
  align-items: stretch;
  flex-direction: row;
  justify-content: space-between;
}

/* settings icon */

.settings-icon {
  display: block;
  position: fixed;
  margin: 20px;
  width: 25px;
  /*background-color: white;*/
  padding: 3px;
  border-radius: 50%;
}
.settings-icon:hover {
  filter: drop-shadow(0 0 0.25rem white);
}

/* Clock In the Corner */

.corner-clock {
  margin: 20px -20px;
  padding: 3px;
  position: fixed;
  right: 50px;
  font-size: 22px;
  color: white;
}

/* Modal */

.modal {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: gray;
  overflow: hidden;
}

.modal * {
  text-align: center;
}

/* Modal X button */

.modal-close {
  position: fixed;
  right: 5px;
  width: 40px;
  padding: 2px;
}

.modal-close:hover {
  filter: drop-shadow(0 0 0.25rem white);
}

/* Rich Context Stuff */

.richContextEditors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.richContextContainer {
  /*padding: 5px;
  margin: 5px;
  background-color: white;
  border: 1px solid black;*/
  margin: 2px;
  display: flex;
}

.editorLabel {
  padding: 2px 2px;
  margin: 0;
}

.richContextEditor {
  height: 20px;
}

.footer {
  margin: 10px 10px;
}
